.taste__text {
  font-weight: 400;
  font-size: 14px;
  line-height: 129%;
  letter-spacing: -0.02em;
  text-align: center;
}
.taste__list {
  display: flex;
  gap: 19px;
  justify-content: center;
}
.taste__subtitle {
  font-weight: 600;
  font-size: 14px;
  line-height: 129%;
  letter-spacing: -0.02em;
  text-transform: uppercase;
  .taste__item:nth-child(even) & {
    color: $text-color;
  }
}
.taste__item {
  border-radius: 15px;
  &:first-child {
    background-color: $accent-color;
  }
}
.taste__item:nth-child(odd) .taste__subtitle {
  color: $white;
}
.taste__item:nth-child(even) {
  border: 2px solid $accent-color;
}
.taste__item:nth-child(3) {
  background-color: black;
}
.taste__overley {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: $white;
  border-radius: 15px;
  transform: translateY(100%);
  transition: transform 250ms ease;
  .taste__item:hover & {
    transform: translateY(0);
  }
}
.taste__wrapper-image {
  overflow: hidden;
  position: relative;
}
.taste__overley-title {
  font-weight: 600;
  font-size: 18px;
  letter-spacing: -0.03em;
  text-transform: uppercase;
  color: $text-color;
}
.taste__overley-text {
  font-weight: 400;
  font-size: 14px;
  line-height: 129%;
  letter-spacing: -0.02em;
  color: $text-color;
}
.taste__overley-btn-div {
  display: flex;
  gap: 6px;
}
.taste__overley-btn {
  border-radius: 35px;
  font-weight: 400;
  font-size: 10px;
  line-height: 120%;
  padding: 6px 16px;
  &:first-child {
    background-color: $accent-color;
    border: transparent;
    color: $white;
  }
}
.taste__overley-btn:last-child {
  background-color: transparent;
  border: 2px solid $accent-color;
  color: $text-color;
}
